"use client";
import React, { useState, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { Button, Text, Input, RadioGroup, Stack, Radio, CheckboxGroup, Checkbox } from "@chakra-ui/react";
import { RegisterFormData } from "./types";

const steps = [
  { key: "email", title: "What's your email?", type: "input", description: "To create or login to your account" },
  { key: "confirmationCode", title: "Enter the confirmation code", type: "input", description: "Check your email." },
  { key: "firstName", title: "What's your first name?", type: "input", description: "Let us know how to address you." },
  { key: "lastName", title: "What's your last name?", type: "input", description: "Share your family name." },
  { key: "dateOfBirth", title: "Enter your date of birth", type: "date", description: "We need this to verify your age." },
  { key: "gender", title: "What's your gender?", type: "radio", options: ["male", "female", "nonbinary"], description: "Select your gender." },
  { key: "lookingFor", title: "What are you looking for?", type: "radio", options: ["marriage", "relationship", "friends", "culture"], description: "Select your intent." },
  { key: "interests", title: "Select your interests", type: "checkbox", options: ["poetry", "prose", "makeup", "climbing", "badminton"], description: "Choose your interests." },
  { key: "photo", title: "Add your first photo", type: "file", description: "Upload a profile picture." }
];

const MainRegister = () => {
  const router = useRouter();
  const searchParams = useSearchParams();
  const stepIndex = parseInt(searchParams.get("step") || "1", 10) - 1;

  const [formData, setFormData] = useState<RegisterFormData>({
    email: "",
    confirmationCode: "",
    firstName: "",
    lastName: "",
    dateOfBirth: "",
    gender: "",
    lookingFor: "",
    interests: [],
    photo: null,
  });

  const updateFormData = (key: string, value: any) => {
    setFormData((prev) => ({ ...prev, [key]: value }));
  };

  const handleNext = () => {
    if (stepIndex < steps.length - 1) {
      router.push(`/register?step=${stepIndex + 2}`);
    } else {
      console.log("Form submitted:", formData);
      router.push("/home");
    }
  };

  const currentStep = steps[stepIndex];

  return (
    <>
      <Text fontSize="2xl" fontWeight="bold">{currentStep.title}</Text>
      <Text mt={2} mb={6}>{currentStep.description}</Text>

      {currentStep.type === "input" && (
        <Input
          placeholder={currentStep.title}
          value={(formData as any)[currentStep.key]}
          onChange={(e) => updateFormData(currentStep.key, e.target.value)}
        />
      )}

      {currentStep.type === "date" && (
        <Input
          type="date"
          value={(formData as any)[currentStep.key]}
          onChange={(e) => updateFormData(currentStep.key, e.target.value)}
        />
      )}

      {currentStep.type === "radio" && (
        <RadioGroup
          value={(formData as any)[currentStep.key]}
          onChange={(value) => updateFormData(currentStep.key, value)}
        >
          <Stack spacing={4}>
            {currentStep.options?.map((option) => (
              <Radio key={option} value={option}>{option}</Radio>
            ))}
          </Stack>
        </RadioGroup>
      )}

      {currentStep.type === "checkbox" && (
        <CheckboxGroup
          value={(formData as any)[currentStep.key]}
          onChange={(value) => updateFormData(currentStep.key, value)}
        >
          <Stack spacing={2}>
            {currentStep.options?.map((option) => (
              <Checkbox key={option} value={option}>{option}</Checkbox>
            ))}
          </Stack>
        </CheckboxGroup>
      )}

      {currentStep.type === "file" && (
        <Input
          type="file"
          accept="image/*"
          onChange={(e) => updateFormData(currentStep.key, e.target.files?.[0])}
        />
      )}

      <Button mt={6} colorScheme="blue" onClick={handleNext}>
        {stepIndex === steps.length - 1 ? "Finish" : "Next"}
      </Button>
    </>
  );
};

export default MainRegister;
